.Header {
  font-size: 2.5rem;
  font-family: "Roboto";
  font-weight: 600;
  padding: 86px 10px 14px 26px;
  /* background: #36b9fb9c; */
  border-radius: 18px;
  background: linear-gradient(#c1e8ff, #fefeff);
  color: darkblue;
}

.TableWrapper {
  height: 90%;
  /* background: linear-gradient(#ffffff, #a0cbff); */
  border-radius: 20px;
  padding: 20px 20px 8px 20px;
  max-height: 82%;
  overflow-y: scroll;
  margin-top: 50px;
}

.SearchBarWrapper {
  padding: 20px 20px 8px 20px;
  height: 12%;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.SearchBarWrapperInner {
  width: 50%;
  /* height: 89%; */
  padding: 4px 8px;
  height: 90%;
}

.SearchBarWrapperInner input {
  width: 100%;

  height: 90%;
  border-radius: 4px;
  border: 1px solid #bdbdbd;
  font-size: 18px;
  padding: 2px 14px;
  font-family: "Roboto";
  font-weight: 400;
  /* height: 89%; */
  /* padding: 8px 16px; */
}
